<template>
    <div>
        <Row>
            <Button type="primary" @click="addBefore" v-permission.show="'b:perm:platform:create'">
                <Icon type="plus"></Icon>新增供应商票务配置
            </Button>
        </Row>
        <Row class="margin-top-10">
            <label class="margin-left-10" style="margin-right:8px;">企业名称：</label>
            <Input v-model="table.query.name" placeholder="" clearable style="width: 200px" />
            <Button class="margin-left-10" type="primary" @click="doQuery" icon="md-search">搜索</Button>
        </Row>
        <Row class="margin-top-10">
            <i-col>
                <Table :columns="table.col" :data="table.data"></Table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="table.total" :show-sizer="true" placement="top" @on-page-size-change="changePageSize" @on-change="changePage"></Page>
                    </div>
                </div>
            </i-col>
        </Row>
        <Modal v-model="model" :mask-closable="false" :title="modelTitle" :loading="loadingBtn" @on-ok="addOrUpdateBefore" @on-cancel="cancel">
            <Form ref="form" :model="vo" :label-width="90">
                <Form-item label="服务类型" prop="type">
                    <Select transfer v-model="vo.service" @on-change="valueChange" >
                        <Option v-for="item in SupplierType" :value="item.id" :key="item.id">{{ item.name }}</Option>
                    </Select>
                </Form-item>
                <Form-item label="授权ID" prop="accessId">
                    <Input v-model="vo.accessId" />
                </Form-item>
                <Form-item label="授权Key" prop="accessKey">
                    <Input v-model="vo.accessKey" />
                </Form-item>
                <Form-item label="企业ID" prop="epId">
                    <Input v-model.trim="vo.epId" />
                </Form-item>
                <Form-item label="企业名称" prop="name">
                    <Input v-model.trim="vo.name" />
                </Form-item>
                <div v-show="showFlag">
                    <Form-item label="地址" prop="link">
                        <al-cascader ref="link" v-model="link"  :level="2" />
                    </Form-item>
                    <Form-item label="额外配置JSON" prop="config">
                        <Input v-model="vo.config" type="textarea" />
                    </Form-item>
                </div>
                <FormItem label="状态" prop="enable" v-if="isUpdate">
                    <i-switch size="large" v-model="vo.enable">
                        <span slot="open">启用</span>
                        <span slot="close">禁用</span>
                    </i-switch>
                </FormItem>
            </Form>
        </Modal>
    </div>
</template>

<script>
import TableDataView from '@/mixins/table-data-view';
import TableColRender from '@/mixins/table-col-render';
import ModelView from '@/mixins/model-view';
import { SupplierType, Status } from '@/libs/dic';
export default {
    name: 'SmsTemplate',
    mixins: [ TableDataView, TableColRender, ModelView ],
    data () {
        return {
            SupplierType,
            Status,
            table: {
                col: [{
                    title: '服务类型',
                    key: 'service',
                    render: (h, params) => this.renderDic(h, params)(SupplierType)
                }, {
                    title: '企业名称',
                    key: 'name'
                }, {
                    title: '地址',
                    key: 'link',
                    tooltip: true
                }, {
                    title: '授权ID',
                    key: 'accessId'
                }, {
                    title: '授权Key',
                    key: 'accessKey'
                }, {
                    title: '额外配置',
                    key: 'config'
                }, {
                    title: '状态',
                    key: 'enable',
                    render: this.renderStatus
                }, {
                    title: '创建时间',
                    key: 'createTime',
                    render: this.renderDate
                }, {
                    title: '更新时间',
                    key: 'updateTime',
                    render: this.renderDate
                }, {
                    title: '操作',
                    key: 'action',
                    width: 240,
                    align: 'center',
                    render: (h, params) => this.renderActions(h, params, [{
                        config: { loading: this.loadingBtn, style: { marginRight: '5px' }, disabled: params.row.type === 0 },
                        click: () => this.updateBefore(params.row),
                        text: '修改'
                    }, {
                        type: 'switch',
                        click: () => this.status(params.row),
                        bool: params.row.enable,
                        config: { loading: this.loadingBtn, style: { marginRight: '5px' } }
                    }, {
                        config: { loading: this.loadingBtn, icon: 'md-trash' },
                        type: 'delete',
                        click: () => this.remove(params.row)
                    }])
                }],
                url: '/api/setting/fxma/list',
                query: {
                    name: null
                }
            },
            link: [],
            showFlag: false,
            vo: {
                epId: null,
                service: null,
                name: null,
                linkCode: null,
                link: null,
                accessId: null,
                accessKey: null,
                config: null,
                enable: null
            },
            addUrl: '/api/setting/fxma/add',
            updateUrl: '/api/setting/fxma/update',
            removeUrl: '/api/setting/fxma/remove'
        };
    },
    computed: {
        generateAddOrUpdate () {
            const last = this.link[this.link.length - 1] || {};
            return { ...this.vo,
                link: last.code
            };
        }
    },
    methods: {
        status (item) {
            Object.keys(this.vo).filter(key => Reflect.has(item, key)).forEach(key => this.vo[key] = item[key]);
            this.vo.enable = !item.enable;
            this.doAddOrUpdate();
        },
        valueChange () {
            if (this.vo.service === 0) {
                this.showFlag = false;
            } else {
                this.showFlag = true;
            }
        },
        addBefore () {
            this.clearInfo();
            this.vo.service = 0;
            this.add();
            this.valueChange();
        },
        updateBefore (rows) {
            this.update(rows);
            this.valueChange();
            if (rows.linkCode) {
                this.setAddress('link', rows.linkCode);
            }
        },
        addOrUpdateBefore () {
            const link = this.link[this.link.length - 1] || {};
            this.vo.linkCode = link.code;
            this.vo.link = link.name;
            this.addOrUpdate();
        },
        setAddress (name, val) {
            if (val.length === 6) {
                this[name] = [
                    val.substring(0, 2).padEnd(6, '0'),
                    val.substring(0, 4).padEnd(6, '0'),
                    val
                ];
            }
        },
        async clearInfo () {
            this.vo.linkCode = null;
            this.vo.link = null;
            this.$nextTick(() => {
                this.$refs.link.$children[0].clearSelect();
            });
        }
    }
};
</script>

